import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { MatButtonModule } from '@angular/material/button';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatInputModule } from '@angular/material/input';
import { MatIconModule } from '@angular/material/icon';
import { MatToolbarModule } from '@angular/material/toolbar';
import { HttpClientModule } from '@angular/common/http';
import { MatSelectModule } from '@angular/material/select';
import { MatDialogModule } from '@angular/material/dialog'
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { ToastrModule } from 'ngx-toastr';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgOptimizedImage } from '@angular/common'
import { LoginComponent } from './pages/login/login.component';
import { MatStepperModule } from '@angular/material/stepper';
import { AdminSignupComponent } from './pages/signup/admin-signup/admin-signup.component';
import { CustomerSignupComponent } from './pages/signup/customer-signup/customer-signup.component';
import { Countries } from './pages/data/countries';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { PopupComponent as Login_Popup } from './pages/login/popup/popup.component'
import { PopupComponent as Signup_Popup} from './pages/signup/popup/popup.component';
import { OopsComponent } from './pages/oops/oops.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    AdminSignupComponent,
    CustomerSignupComponent,
    Login_Popup,
    Signup_Popup,
    OopsComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatInputModule,
    MatIconModule,
    MatToolbarModule,
    HttpClientModule,
    FormsModule,
    Ng2SearchPipeModule,
    ReactiveFormsModule,
    MatSelectModule,
    MatDialogModule,
    ToastrModule.forRoot(),
    NgbModule,
    NgOptimizedImage,
    MatStepperModule
  ],
  providers: [
    { provide: 'COUNTRIES', useValue: Countries },
    { provide: LocationStrategy, useClass: HashLocationStrategy }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
