import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AdminSignup, CustomerSignup, LocationDetails } from './signup.model';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class SignupService {

  baseApiUrl: string = environment.baseApiUrl;

  constructor(private http: HttpClient) { }

  AdminSignup(AdminsignUp: AdminSignup): Observable<AdminSignup[]> {
    return this.http.post<AdminSignup[]>(this.baseApiUrl + '/api/Signup/AdminSignup', AdminsignUp)
  }

  CustomerSignup(customerSignup: CustomerSignup): Observable<CustomerSignup[]> {
    return this.http.post<CustomerSignup[]>(this.baseApiUrl + '/api/Signup/CustomerSignup', customerSignup)
  }
}
