import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './pages/login/login.component';
import { AdminSignupComponent } from './pages/signup/admin-signup/admin-signup.component';
import { CustomerSignupComponent } from './pages/signup/customer-signup/customer-signup.component';
import { AuthGuard } from './auth.guard';
import { OopsComponent } from './pages/oops/oops.component';

const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'signup/admin', component: AdminSignupComponent },
  { path: 'signup/customer', component: CustomerSignupComponent },
  { path: 'admin', canActivate: [AuthGuard], loadChildren: () => import('./pages/admin/admin.module').then((m) => m.AdminModule)},
  { path: 'customer', canActivate: [AuthGuard], loadChildren: () => import('./pages/customer/customer.module').then((m) => m.CustomerModule)},
  { path: '', redirectTo: '/login', pathMatch: 'full'},
  { path: '**', component: OopsComponent }
]; 

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
